import React from "react";
import WriteContentComp from "../../../../main/board/writecontent/WriteContentComp";
import { useDispatch, useSelector } from "react-redux";

import { POST_CONTENT } from "../../../../../store/features/contentSlice";
import useNavigateGuard from "../../../../../hooks/useNavigateGuard";

function WriteContentPage() {
  const dispatch = useDispatch();
  const isAuth = useSelector((state) => state.auth)
  useNavigateGuard("/auth/login",!isAuth)


  const onSubmit = () => {
    dispatch(POST_CONTENT());
  };

  return <WriteContentComp onSubmit={onSubmit} />;
}

export default WriteContentPage;
