import React from "react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import { useDispatch, useSelector } from "react-redux";
import { SET_CONTENT, SET_TITLE } from "../../../../store/features/boardSlice";
import { POST_CONTENT } from "../../../../store/features/contentSlice";
import { toast } from "react-toastify";
import { useNavigate } from "react-router";

function WriteContentComp() {
  const { postTitle, postContent } = useSelector((state) => state.board);
  const navigate = useNavigate()
  const dispatch = useDispatch();
  return (
    <div>
      <div className="page_title">글쓰기</div>
      <input
        type="text"
        name="postTitle"
        onBlur={(e) => dispatch(SET_TITLE(e.target.value))}
        placeholder="제목"
        className="mb-4 w-full border-b border-gray-300 p-4 text-2xl"
      />
      <CKEditor
        editor={ClassicEditor}
        className="ckeditor"
        onBlur={(event, editor) => {
          const data = editor.getData();
          dispatch(SET_CONTENT(data));
        }}
      />
      <button
        type="button"
        className={"confirm-btn"}
        onClick={() => {
          dispatch(POST_CONTENT({ postTitle, postContent }))
            .unwrap()
            .then((res) => {
              navigate(`../viewcontent/${res.postNo}`)
              toast.success("글을 성공적으로 작성했습니다.")
            });
        }}
      >
        작성하기
      </button>
    </div>
  );
}

export default WriteContentComp;
