import { useNavigate } from "react-router";
import { useEffect } from "react";

/**
 * Navigation Guard
 * @param target
 * @param state
 */

const useNavigateGuard = (target, state) => {
  const navigate = useNavigate();
  useEffect(() => {
    if (state) {
      navigate(target, { replace: true });
    }
  }, []);
};

export default useNavigateGuard;
